.App {
  display: block;
  margin: 0 auto;
  max-width: 580px;
  text-align: center;
}

.App-header {
  display: block;
  height: 60px;
  margin: 0;
  padding: 0;
  background-color: red;
}
.App-main {
  display: block;
  margin: 0;
  padding: 10px;
  height: calc(100vh - 100px);
}
.btn {
  display: inline-block;
  background-color: #fff;
  margin: 3px;
  width: 50px;
  height: 50px;
  border-radius: 25%;
}
.btn:active,
.btn:focus {
  outline: none;
}
pre {
  /*margin-left: -35px;*/
  font-size: 100%;
}
